/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Main nav */
nav.navbar.navbar-default { border: none; margin-bottom: 0; padding: 0 2em; border-radius: 0; }

@media only screen and (max-width: 400px) { nav.navbar.navbar-default { padding: 0 1em; } }

@media only screen and (max-width: 400px) { nav.navbar.navbar-default h1 { font-size: 1em; } }

nav.navbar.navbar-default ul.nav.nav-pills { margin-top: 1em; border: none; }

nav.navbar.navbar-default ul.nav.nav-pills li a:active, nav.navbar.navbar-default ul.nav.nav-pills li a:hover, nav.navbar.navbar-default ul.nav.nav-pills li a.dropdown-toggle:active, nav.navbar.navbar-default ul.nav.nav-pills li a.dropdown-toggle:hover { background-color: transparent; border: 0; }

nav.navbar.navbar-default ul.nav.nav-pills li.dropdown.open { background-color: transparent; }

nav.navbar.navbar-default ul.nav.nav-pills li.dropdown.open a { background-color: transparent; }

nav.navbar.navbar-default ul.nav.nav-pills li.active a { background-color: transparent; }

nav.navbar.navbar-default ul.dropdown-menu { border: none; box-shadow: none; border-radius: 0; margin-top: 0; }

/* Paging navs */
@media only screen and (max-width: 400px) { #exhibit-page-navigation li a, .pager li a { width: 100%; margin-bottom: 1em; } }

/* Footer nav */
@media only screen and (max-width: 400px) { .footer-menu { display: none; } }

